export type ColumnType = 'STRING' | 'NUMBER' | 'SHORT_NUMBER';

/**         */
export enum ColumnTypesEnum {
  STRING = 'STRING',
  NUMBER = 'NUMBER',
  SHORT_NUMBER = 'SHORT_NUMBER',
}

export interface IColumn {
  /**   */
  template?: ColumnTypesEnum | Function,
  /**    */
  properties?: string,
  /**   */
  title?: string,
  /**     */
  prefixes?: string,
  /**     (%, $  ..) */
  suffixes?: string,
  /**       */
  formatter?: Function,

  /**   */
  sortDirection?: number,

  /**   */
  isFixed?: boolean,
  /**   */
  isSortable?: boolean | string,
  /**    */
  isResizable?: boolean,
  /** Drag-and-Drop  */
  isDraggable?: boolean,
  /**   */
  isShow?: boolean,
  /**    */
  className?: string | Function
  /**    */
  style?: Function,
  /**       */
  minWidth?: number,
  /**      */
  defaultWidth?: number,
  /**     */
  userWidth?: number,
  /**    */
  width?: number,
  /**    */
  onClick?: Function,
}
